import React, { useState, useContext } from "react";
import { NavLink } from "react-router-dom";
import ThemeToggle from "./ThemeToggle";
import { ThemeContext } from "../context/ThemeContext";
import "../styles/header.css";
import Dropdown from "react-bootstrap/Dropdown";

function Header() {
  const [isNavOpen, setIsNavOpen] = useState(false);
  const { isDarkMode, toggleTheme } = useContext(ThemeContext);

  const toggleNav = () => setIsNavOpen(!isNavOpen);
  const closeNav = () => setIsNavOpen(false);

  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const toggleDropdown = () => setIsDropdownOpen(!isDropdownOpen);
  const closeDropdown = () => setIsDropdownOpen(false);
  return (
    <header className="header">
      <div className="logo">
        <NavLink to="/">
          {/*           <img src={logo} alt="Brave Sparkids Logo" /> */}
          <svg
            id="mySvg"
            width="95"
            height="110"
            viewBox="0 0 295 409"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
            xmlnsXlink="http://www.w3.org/1999/xlink"
          >
            <rect
              id="myrect"
              width="295"
              height="410"
              fill="url(#pattern0_1357_198322)"
            />
            <defs>
              <pattern
                id="pattern0_1357_198322"
                patternContentUnits="objectBoundingBox"
                width="1"
                height="1"
              >
                <use
                  xlinkHref="#image0_1357_198322"
                  transform="scale(0.00338983 0.00244499)"
                />
              </pattern>
              <image
                id="image0_1357_198322"
                width="295"
                height="409"
                xlinkHref="data:image/png;base64,"
              />
            </defs>
          </svg>
        </NavLink>
      </div>
      <div className="hamburger" onClick={toggleNav}>
        <span></span>
        <span></span>
        <span></span>
      </div>
      <nav className={`nav ${isNavOpen ? "active" : ""}`}>
        <button onClick={toggleTheme}>
          {isDarkMode ? "🌞 Mode" : "˚☽˚.⋆ Mode"}
        </button>   {/*Uncomment this to add a theme toggle button */}
        <ul>
          <li>
            <NavLink
              to="/"
              onClick={closeNav}
              className={({ isActive }) => (isActive ? "active" : "")}
            >
              Home
            </NavLink>
          </li>
          <li>
            <NavLink
              to="/about"
              onClick={closeNav}
              className={({ isActive }) => (isActive ? "active" : "")}
            >
              AboutUs
            </NavLink>
          </li>
          <li
            className={`dropdown ${isDropdownOpen ? "open" : ""}`}
            onMouseEnter={toggleDropdown}
            onMouseLeave={closeDropdown}
          >
            <button className="dropdown-button" style={{ display: "flex" }}>
              Courses
              <span className={`header-arrow ${isDropdownOpen ? "open" : ""}`}>
                <svg
                  width="12"
                  height="13"
                  viewBox="0 0 12 13"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M2 10.5L8 6.5L2 2.5"
                    stroke="#F58634"
                    stroke-width="3.37"
                    stroke-linecap="round"
                  />
                </svg>
              </span>
            </button>
            {isDropdownOpen && (
              <ul
                className="header-dropdown-menu"
                style={{
                  margin: "0",
                  gap: "10px",
                  alignItems: "left",
                }}
              >
                <li>
                  <NavLink to="/abacus" onClick={closeNav}>
                    Abacus
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/vedic" onClick={closeNav}>
                    Vedic Maths
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/rubic" onClick={closeNav}>
                    Rubik
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/handwriting" onClick={closeNav}>
                    Handwriting
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/reading" onClick={closeNav}>
                    Reading
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/courses" onClick={closeNav}>
                    All Courses
                  </NavLink>
                </li>
              </ul>
            )}
          </li>
          <li>
            <NavLink
              to="/franchise"
              onClick={closeNav}
              className={({ isActive }) => (isActive ? "active" : "")}
            >
              Franchise
            </NavLink>
          </li>
          <li>
            <NavLink
              to="/careers"
              onClick={closeNav}
              className={({ isActive }) => (isActive ? "active" : "")}
            >
              Careers
            </NavLink>
          </li>
          <li>
            <NavLink
              to="/insight"
              onClick={closeNav}
              className={({ isActive }) => (isActive ? "active" : "")}
            >
              Insights
            </NavLink>
          </li>
          <li>
            <NavLink
              to="/contact"
              onClick={closeNav}
              className={({ isActive }) => (isActive ? "active" : "")}
            >
              <button className="contact-us">Contact Us</button>
            </NavLink>
          </li>
        </ul>
      </nav>
      <div
        className={`overlay ${isNavOpen ? "active" : ""}`}
        onClick={closeNav}
      ></div>
    </header>
  );
}

export default Header;
